<template>
    <div id="gloBox">
        <div id="d1" class="dslb">
			<div id="d2">
				<div id="d3">
					<div id="d4">
					</div>
				</div>
				<div id="d5" class="txal w100pc"><span style="background-color:#fd0000;">{{this.$store.state.user.name || '000'}}</span></div>
			</div>
		</div>
    </div>
</template>

<script>
    export default {
        created() {
            
        },
    }
</script>

<style lang="less" scoped>
    #d1{ 
        animation: run 3s linear 0s infinite;
		position: fixed;
        width: 100px;height: 100px;background-color: #000;position: relative;border: 5px dashed #fd0000;border-radius: 50%;
    }
    @keyframes run{
        from{transform: rotate(0deg);}
        to{transform: rotate(360deg);}
    }
    @-webkit-keyframes run{
        from{transform: rotate(0deg);}
        to{transform: rotate(360deg);}
    }
    #d2{animation: run3 2s linear 0s infinite;
        position: fixed;
        width: 80px;height: 80px;background-color: #000000;position: absolute;top: 10px;left: 10px;}
    @keyframes run3{
        from{transform: rotate(360deg);}
        to{transform: rotate(0deg);}
    }
    @-webkit-keyframes run3{
        from{transform: rotate(360deg);}
        to{transform: rotate(0deg);}
    }
    #d3{
        width: 60px;height: 60px;background-color:#000;position: absolute;top: 7px;left: 7px;border: 3px solid #fd0000;border-radius: 50%;
    }
    #d4{animation: run 1s linear 0s infinite;
        position: fixed;
        width: 40px;height: 40px;background-color:#101010;position: absolute;top: 10px;left: 10px;}
    #d5{
        animation: run 3s linear 0s infinite;
        position: fixed;
        
        position: absolute;
        top: 30px;left: 0px;
    }
</style>


